/*
 * CSS Opinioned Reset
 */

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  max-height: 100%;
}

nav ul {
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
}

nav ul li {
  display: block;
}

/*
 * Overall Layout
 */
body {
  padding: 1rem;
  margin: 0;
  min-width: 320px;
  font-family: sans-serif;
}

.header {
  h1 {
    margin-top: 0.5rem;
  }

  .subtitle {
    margin-top: 1rem;
    margin-bottom: 2rem;
  }
}

.hidden {
  display: none !important;
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.8);
}

/*
 * Navigation
 */
.links {
  line-height: 2.3rem;
}

.links a {
  font-size: 1.3rem;
  color: black;
}

.links a:hover {
  opacity: 0.5;
}

.overlay .links {
  padding: 2rem;
}

/*
 * Footer
 */
.footer {
  .social {
    padding: 1rem 0;
  }

  .social a {
    padding-right: 0.25rem;
  }

  .social .icon svg {
    width: 2rem;
    height: 2rem;
  }

  .social .username {
    display: none;
  }

  .copyright {
    font-size: 0.8rem;
  }
}

@media screen and (max-width: 767px) {
  .header {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: baseline;
    padding-top: 0.5rem;
  }

  .header {
    .title {
      order: 1;
      flex: 1;
    }

    .nav {
      order: 1;
    }

    .subtitle {
      order: 2;
      width: 100%;
      margin-bottom: 1rem;
    }
  }

  .footer {
    padding: 1rem 0;
  }

  /*
   * Navigation
   */
  .header .nav {
    .links {
      display: none;
    }

    .menu {
      position: relative;
      width: 24px;
      height: 20px;
      cursor: pointer;
    }

    .menu {
      .icon, .icon:before, .icon:after {
        position: absolute;
        width: 100%;
        height: 3px;
        background-color: black;
        content: '';
      }

      .icon {
        top: 8px;
      }

      .icon:before {
        content: '';
        top: -8px;
      }

      .icon:after {
        content: '';
        top: 8px;
      }
    }
  }
}

@media screen and (min-width: 768px) {
  body {
    display: flex;
    flex-flow: column wrap;
    align-items: flex-start;
    height: 100vh;
  }

  .header, .footer {
    width: 30%;
    order: 1;
    padding-left: 1rem;
    padding-right: 1rem;
  }

  .header {
    flex: 1;
    padding-top: 1rem;
  }

  .footer {
    padding-bottom: 1rem;
  }

  .main {
    width: 70%;
    order: 2;
    flex: 1 0 100%;
    overflow: auto;
  }

  /*
   * Navigation
   */
  .header .menu {
    display: none;
  }
}

@media screen and (min-width: 1024px) {
  .header, .footer {
    padding-left: 3rem;
    padding-right: 3rem;
  }

  .header {
    padding-top: 3rem;
  }

  .footer {
    padding-bottom: 3rem;
  }
}
