.home-content {
  width: 100%;
}

.home-content {
  .pager {
    padding: 1rem 0;
  }

  .pager {
    ul {
      display: flex;
      justify-content: space-between;
    }

    li {
      display: block;
      text-align: center;
      font-size: 1.3rem;
      width: 50%;
      padding: 0 0.5rem;
    }

    a {
      display: block;
      width: 100%;
      height: 100%;
      color: white;
      background-color: black;
      padding: 1rem;
    }
  }

  .post {
    width: 50%;
    padding: 0.5rem;
    position: relative;
  }

  .post {
    img {
      width: 100%;
      display: block;
    }

    header {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      padding: 0.5rem;
      display: none;
    }

    &:hover header, &.no-image header {
      display: block;
    }

    header a {
      width: 100%;
      height: 100%;
      padding: 0.5rem;
      margin: 0;
      display: flex;
      flex-flow: column;
      justify-content: center;
      align-items: center;
      color: white;
      background-color: rgba(0, 0, 0, 0.6);
    }

    .place-holder {
      width: 100%;
      height: 15rem;
      background-color: gray;
    }
  }
}

@media screen and (max-width: 767px) {
  .home-content {
    margin: 0 -0.5rem;
  }
}
