.gallery {
  ul {
    list-style: none;
    width: 100%;
    padding: 0;
    margin: 0 -0.25rem;
    display: flex;
    flex-flow: row wrap;
  }

  li {
    display: block;
    height: 20vh;
    padding: 0.25rem;
    cursor: pointer;
  }

  img {
    height: 100%;
  }

  .gallery-overlay img {
    height: auto;
  }

  .gallery-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(255, 255, 255, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
  }
}
